/* Hero Section - Minimal Module Styles */

.heroSection {
    padding: 1.5rem;
    margin-bottom: 1rem;
    text-align: center;
}

.heroContent {
    max-width: 900px;
    margin: 0 auto;
}

.heroTitle {
    margin: 0 0 0.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 0.75rem;
}

.heroSubtitle {
    margin: 0 auto 2rem;
    max-width: 600px;
}

/* Hero Form */
.heroForm {
    padding: 2rem;
    max-width: 800px;
    margin: 0 auto;
}

.formGroupInline {
    display: flex;
    gap: 1.5rem;
    margin-bottom: 2rem;
    flex-wrap: nowrap;
    align-items: center;
    justify-content: center;
}

.inputWrapper {
    flex: 1;
    min-width: 0;
    text-align: left;
    display: flex;
    flex-direction: column;
}

.inputLabel {
    display: block;
    margin-bottom: 0.6rem;
}

.dateInputHero {
    width: 90%;
    height: 48px;
    padding: 0 8px;
    text-align: center;
}

.getPanchangBtnHero {
    width: auto;
    min-width: 280px;
    margin: 0 auto;
    padding: 12px 32px;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 0.75rem;
}

.resultsSection {
    padding: 0 1.5rem;
}

/* Error box */
.errorBoxHero {
    padding: 1rem;
    margin: 1rem 0;
    text-align: center;
}

/* Mobile Responsive */
@media (max-width: 768px) {
    .heroSection {
        padding: 3rem 1.5rem;
    }

    .heroTitle {
        flex-direction: column;
        gap: 0.5rem;
    }

    .heroForm {
        padding: 1.5rem;
    }

    .formGroupInline {
        flex-wrap: wrap;
    }

    .inputWrapper {
        min-width: 100%;
    }
}

@media (max-width: 480px) {
    .heroForm {
        padding: 1rem;
    }

    .dateInputHero {
        height: 64px;
        padding: 10px 16px;
    }

    .getPanchangBtnHero {
        padding: 14px 24px;
    }
}
